<template>
  <div class="Home">
   	<div class="content">
   		 <ul class="leftList">
  	    	<li v-for="(singList,index) in leftList" :class="{'active':index == activedIndex}" @click="changeIndex(index)">
            <p>{{singList.name}}</p>
            <span class="totalNumIcon" v-if="singList.totalCountOff">{{singList.totalCount}}</span>
          </li>
  	    </ul>
          <div class="rightLists" @scroll="listScroll($event)" ref="rightLists">
              <div class="sectionList" v-for="(singList,index) in leftList" ref="lists">
                <dl>{{singList.name}}</dl> 
                <dt v-for="(item,singleIndex) in singList.foods" class="smallList">
                    <p>{{item.name}}</p>
                    <p>{{item.description}}</p>
                    <div class="icons">
                      <i class="iconfont icon-jianhao" v-if="item.off" @click="subtract(index,singleIndex)"></i>
                      <span v-if="item.count>0">{{item.count}}</span>
                      <i class="iconfont icon-jiahao" @click="increase(index,singleIndex)"></i>
                    </div>
                    
                </dt>
              </div>  
           
          </div>
  	    
   	</div>
   
  </div>
</template>

<script>
import leftList from '../data/foodsJson.js'
export default {

  name: 'Home',
  data () {
    return {
      leftList,
      activedIndex:0, //被选中下标
      listHeight:[],   //列表高度集合数组
    }
  },
  methods:{
    changeIndex:function(index){
      //选中下标
      this.activedIndex = index;
      //右侧对应被选中的列表项
      let sectionList = this.$refs.lists[index];
      //右侧容器节点
      let rightLists = this.$refs.rightLists; 
      clearInterval(scrollTimer);     
      //滑动动画
      let scrollTimer = setInterval(function(){
        if (rightLists.scrollTop + rightLists.offsetTop <= sectionList.offsetTop) {
            rightLists.scrollTop += 5;
            if (rightLists.scrollTop + rightLists.offsetTop >= sectionList.offsetTop || rightLists.scrollTop + rightLists.clientHeight >= rightLists.scrollHeight) {
                clearInterval(scrollTimer);
             }
        }else{
          if (rightLists.scrollTop + rightLists.offsetTop <= sectionList.offsetTop) {
              clearInterval(scrollTimer);
          }
          rightLists.scrollTop -= 5; 
        }
      },1);

    },
    listScroll(event){
        this.listHeightAll;
        //滚动距离
        let scrollValue = event.srcElement.scrollTop;
        let length = this.listHeight.length;
        for(let i = 0;i<length-1;i++){
            let height1 = this.listHeight[i];
            let height2 = this.listHeight[i+1];
            if(!height2 || (scrollValue >= height1 && scrollValue < height2)){
              this.activedIndex = i;
            }
        }  
    },
    increase(index,singleIndex){
      this.leftList[index].foods[singleIndex].count += 1;
      this.leftList[index].foods[singleIndex].off = true;
      this.common(index);

    },
    subtract(index,singleIndex){
      this.leftList[index].foods[singleIndex].count -= 1;
      if(this.leftList[index].foods[singleIndex].count == 0){
        this.leftList[index].foods[singleIndex].off = false; 
      }
      this.common(index);
    },
    common(index){
      let num = 0;
      let lists = this.leftList[index];
      let listFood = lists.foods;
      listFood.forEach((item) => {
        num += item.count;
      })
      lists.totalCount = num;
      if(lists.totalCount > 0){
          lists.totalCountOff = true;
      }else{
          lists.totalCountOff = false;
      }
    }
  },
  computed:{
    listHeightAll(){
      //右侧列表dom对象集合
      let foodList = this.$refs.lists;
      let height = 0;
      this.listHeight.push(height);
      for(let i = 0;i < foodList.length;i++){
          let itemList = foodList[i];
          height += itemList.offsetHeight;
          this.listHeight.push(height);
      }
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import url("//at.alicdn.com/t/font_508700_ay0shufmrilwstt9.css");

  .content{
    display: flex;
  }
  .leftList{
    margin-right: 0.25rem;
  }
  .leftList li{
  	width:5rem;
  	height:2rem;
  	line-height: 2rem;
    text-align: center;
    margin:.25rem;
    position: relative;
  	border:1px solid #ccc;
  }
  .active{
    background-color: #e8e8e8;
  }

  .rightLists{
    height:20rem;
    overflow-y: scroll;
  }
  .sectionList{
    width:10rem;
    font-size: .8rem;
  }
  .sectionList dl{
    height:2rem;
    line-height: 2rem;
    background-color: skyblue;
  }
  .smallList{
    height:4rem;
    border:1px solid red;
    position: relative;
  }

  i{
    font-size: 1rem;
    color:#17abe3;
  }
  .icons{
    position: absolute;
    bottom: 5px;
    right:5px;
  }
  .totalNumIcon{
    display: inline-block;
    width:1rem;
    height:1rem;
    line-height: 1rem;
    position: absolute;
    right:1px;
    top:0;
    border-radius: 50%;
    background-color: lightblue;
  }
</style>



